.formContainer {
  border-radius: 25px;
  border: 4px dashed #9f1fe9;
  width: 100%;
  height: 80%;
  max-width: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.formContainer input {
  font-size: 2rem;
  font-weight: 600;
}

.form-control {
  background-color: transparent;
  color: black;
  border-bottom: 2px solid black !important;
  border: none;
  box-shadow: none;
  border-radius: 0;
}

.form-control::-webkit-input-placeholder {
  color: rgb(91, 91, 91);
}

