.nav_bar {
  list-style-type: none;
  height: 50px;
  font-size: 1.8rem;
  position: fixed;
  width: 100%;
  z-index: 10;
  transition: height 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    background-color 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

  @media (min-width: 1000px) {
    height: 88px;
  }
}

@media (min-width: 1000px) {
  .nav_bar-offset-crossed {
    background-color: #57058b;
    height: 72px;
    box-shadow: 0 8px 16px 0 rgba(145, 158, 171, 0.16);
  }
}

.navLogo,
.s-open,
.s-close {
  display: none;
}
.nav-inner {
  width: 30%;
}
ul {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  height: 100%;
  align-items: center;
}
nav ul li {
  display: inline-block;
}
.links {
  color: rgb(231, 228, 228);
  transition: 0.5s;
  font-weight: 300;
  letter-spacing: 1.3px;

  &:hover {
    opacity: 0.8;
  }
}

@media (max-width: 1000px) {
  .ease {
    background: #282828;
    height: 75%;
    opacity: 0.2;
    pointer-events: none;
  }
  ul {
    flex-direction: column;
    padding: 30px 20;
  }

  .navLogo {
    width: 50px;
    display: block;
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: -1;
  }
  .s-open {
    display: inline;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: -1;
  }
  .s-close {
    display: block;
    position: absolute;
    right: 10px;
    top: 10px;
  }
}
